<template>
  <div class="app-index">
    <!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh"> -->
    <!-- header begin -->
    <van-sticky>
      <div class="xy-header">
        <div class="xy-header-left">
          <div class="xy-header-tx" @click="showImage(userInfo.tx_url?userInfo.tx_url:txurl)">
            <van-image
              :src="userInfo.tx_url?userInfo.tx_url:txurl"
              :round="true"
            />
          </div>
        </div>
        <div class="xy-header-name">
          <em>{{ username }}</em>
        </div>
        <!-- <div class="xy-header-right">
          <i class="iconfont iconsaomiao"></i>
        </div> -->
      </div>
    </van-sticky>
    <!-- header end -->

    <div class="index-slide lineGradient">
      <div class="index-slide-content">
        <van-swipe
          ref="swiper"
          :is-prevent="false"
          :use-native-driver="false"
        >
          <van-swipe-item v-for="(item, $index) in slide" :key="$index">
            <div class="slide-item">
              <div class="slide-h2">{{ item.text }}</div>
              <div class="slide-h3">{{ item.text1 }}</div>
              <!-- 插入图片 -->
              <div class="slide-img"><van-image :src="item.imgUrl" /></div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <!-- banner end -->
    <!-- 功能区 beign -->
    <div class="index-main jbr-index-main">
      <div class="function-item box">
        <div v-if="cdArray.length<=8">
          <div :class="cdArray.length>3 ?'function-list function-list-4': 'function-list'">
            <dl
              v-for="(item, index) in cdArray"
              :key="index"
            >
              <dt @click="goPage(item.component)">
                <i :class="'iconfont '+ item.icon" />
              </dt>
              <dd>{{ item.meta.title }}</dd>
            </dl>
          </div>
        </div>
        <van-swipe v-else class="my-swipe" :stop-propagation="false">
          <van-swipe-item v-for="(cdItem, cdIndex) in cdSwipe" :key="cdIndex">
            <div class="function-list function-list-4">
              <dl
                v-for="(item, index) in cdItem"
                :key="index"
              >
                <dt @click="goPage(item.component)">
                  <i :class="'iconfont '+ item.icon" />
                </dt>
                <dd>{{ item.meta.title }}</dd>
              </dl>
            </div>

          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <!-- 功能区 end -->

    <!-- 最新公告 begin -->
    <!-- <div class="content">
      <div class="index-notice">
        <van-notice-bar left-icon="volume-o" :scrollable="false" background="#fff">
          <van-swipe
            vertical
            class="notice-swipe"
            :autoplay="3000"
            :show-indicators="false"
          >
            <van-swipe-item>关于公司组织机构调整及相关管理人员任命决定</van-swipe-item>
            <van-swipe-item>2021年各月周六工作日</van-swipe-item>
            <van-swipe-item>2020年度员工年假结余公示</van-swipe-item>
          </van-swipe>
        </van-notice-bar>
      </div>
    </div> -->
    <!-- 最新公告 end -->

    <!-- <button @click="click">tttt</button> -->
    <div v-for="(zm,index) in zmList" :key="index">
      <p v-html="zm.zmhtml" />
    </div>
    <!-- </van-pull-refresh> -->
    <div
      v-if="cdArray.length===0"
      class="content"
      style="position: relative;z-index: 10;margin-top:-10px;"
    >
      <div class="common-main box">
        <div class="error-main error-main-page">
          <div class="status">
            <div class="status-icon no-xiangmu" />
            <h3>暂无数据</h3>
          </div>
        </div>
      </div>
    </div>
    <!-- 版本信息 begin -->
    <div class="mine-copyright">当前版本: {{ version }}</div>
    <!-- 版本信息 end -->
    <FooterNav ref="FooterNav" />
  </div>

</template>
<script land="script">
import FooterNav from '@/components/footerNav/footerNav.vue'
import { pageRouter } from '@/common/js/mixin.js'
import { mapState } from 'vuex'
import { getTxAction } from '../../../store/jbr/dbsx'
import { Dialog } from 'vant'
import { getVantCd, getZmMoudle, getMoudleHtml } from '@/store/module.js'
import { getToken } from '@/common/js/catch'
import { ImagePreview } from 'vant'
// import wx from 'weixin-js-sdk'
// import sdk from '@/common/js/sdk' // 引入sdk.js
export default {
  FixedHeader: false,
  name: 'MhHome',
  components: {
    FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      menuList: '',
      version: process.env.VUE_APP_VERSION,
      more: {
        component: 'mhmore',
        hidden: false,
        icon: 'iconshenglvehao',
        meta: { title: '更多' },
        name: 'more',
        needLogin: false,
        opened: false,
        path: 'MhMore',
        showMessage: false
      },
      homeid: 'mhhomeid',
      scrollY: true,
      listdirection: 'up',
      isLoading: false,
      finished: true,
      loading: false,
      username: '',
      cdArray: [],
      cdSwipe: [],
      zmList: [],
      slide: [
        {
          imgUrl: require('@/assets/images/index/banner1.png'),
          text: '信源信息',
          text1: '招采软件选信源 深耕招采领域16年'
        },
        {
          imgUrl: require('@/assets/images/index/banner2.png'),
          text: '信源企业e招采',
          text1: '前瞻技术架构、广泛适用的应用场景'
        },
        {
          imgUrl: require('@/assets/images/index/banner3.png'),
          text: '大数据深度挖掘分析',
          text1: ' 云计算灵活部署，引领IT时代潮流'
        }
      ],
      FixedHeader: false,
      txurl: require('@/assets/images/default-tx.png')
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      msgCount: state => state.router.msgCount
    })
  },
  watch: {
    zmList: function(v, oldv) {
      this.getZmNrList()
    }
  },
  created() {
    const self = this
    window.goPage = self.goPage
    window.goDbrwxx = self.goDbrwxx
    window.goXtggXq = self.goXtggXq
    window.kkview = self.kkview
    window.itemClick = self.itemClick
    window.goMoreJqjyxm = self.goMoreJqjyxm
    this.username = this.userInfo.name + '-' + this.userInfo.bmmc
    // if (JSON.parse(window.localStorage.getItem('isFirst')) === 1) {
    //   this.getCdList()
    // }
    this.getCdList()
    this.getZmList()
    this.getXlsl()
    this.$store.commit('router/SET_Foot_Num', 0)
  },
  methods: {
    // 向小程序发送消息
    // click() {
    //   sdk.getJSSDK('')
    //   wx.miniProgram.postMessage({ data: { title: '我这里是标题网页标题。', desc: '我这里是网页描述。' }})
    // },
    showImage(imagePath) {
      ImagePreview({
        images: [
          imagePath
        ],
        startPosition: 0,
        onClose() {
          // do something
        }
      })
    },
    kkview(url) {
      window.open('http://193.193.193.35:8012/onlinePreview?url=' + encodeURIComponent(url))
    },
    getXlsl() {
      this.$store.dispatch('jbrdbsx/getWdslAction', {
        userid: this.userInfo.id,
        apptype: 'APP'
      }).then(res => {
        this.$store.commit('router/SET_MsgCount', res)
      })
    },
    getCdList() {
      const _this = this
      getVantCd.param = 'token=' + getToken()
      getVantCd.data = {
        ryId: this.userInfo.id
      }
      this.asyncApiModule(getVantCd).then(res => {
        res = JSON.parse(res)
        if (res.router.length === 0) {
          this.$toast.fail('暂无数据！')
        } else {
          let templist = []
          const cdDatas = res.router[0].children
          this.cdArray = cdDatas
          if (cdDatas.length > 8) {
            for (let i = 0; i < res.router.length; i++) {
              var child = res.router[0].children
              child.forEach(function(v, j) {
                if (j !== 0 && j % 7 === 0) {
                  templist.push(v)
                  _this.cdSwipe.push(templist)
                  templist = []
                } else {
                  templist.push(v)
                }
              })
            }
            if (templist.length > 0) {
            // templist.push(this.more)
              this.cdSwipe.push(templist)
            }
            console.info('cdSwipe====', this.cdSwipe)
          }
        }
      }).catch(error => {
        console.info('res.router======', error)
        this.$toast.fail(error)
      })
    },
    goDbrwxx(id) {
      getTxAction({
        id: id
      }).then(resTx => {
        this.$store.dispatch('jbrdbsx/setDbsxlistItem', resTx)
        this.$store
          .dispatch('jbrdbsx/updateTxZtAction', {
            id: id
          })
          .then(res => {
            this.loading = false
            let txfs = resTx.txfs
            if (resTx.ta && resTx.ta !== '') {
              txfs = resTx.ta
            }
            if (resTx.status === 2) {
              this.$store.dispatch('jbrdbsx/getWdslAction', {
                userid: this.userInfo.id,
                apptype: 'APP'
              }).then(res => {
                this.$store.commit('router/SET_MsgCount', res)
              })
            }
            if (txfs === 3) { // 流程处理
              this.$store.dispatch('jbrdbsx/setTxbizid', resTx.bizId)
              this.$router.push({
                name: 'DbsxXxxx',
                params: { title: '待办详情' }
              })
            } else if (txfs === 2) { // 功能中处理
              Dialog.alert({
                title: resTx.processName,
                message: ((resTx.txnr === resTx.processName) ? '' : resTx.txnr) + '\n该任务需前往PC端进行办理！'
              }).then(() => {
              })
            } else { // 普通提醒
              Dialog.confirm({
                message: resTx.txnr,
                confirmButtonText: '已阅'
              }).then(() => {
                this.$store.dispatch('jbrdbsx/dealTxActions', {
                  ywbh: resTx.bizId
                }).then(res => {
                  setTimeout(() => {
                    this.getZmList()
                  }, 2000)
                }).catch(() => {
                })
              }).catch(() => {
                // on cancel
              })
            }
          })
          .catch(() => {
            // this.loading = false
          })
      })
    },
    // 跳转公告页面
    goXtggXq(id) {
      this.$router.push({
        name: 'XtggGgxq',
        params: {
          id: id
        }
      })
    },

    goPage(page) {
      this.menuList = this.$store.state.router.footMeun
      for (let i = 0; i < this.menuList.length; i++) {
        const menu = this.menuList[i]
        if (menu.component === page) {
          this.$store.commit('router/SET_Foot_Num', i)
        }
      }
      // if (page === 'XtggIndex') {
      //   this.$store.commit('router/SET_Foot_Num', 3)
      // }
      // if (page === 'DbsxIndex') {
      //   this.$store.commit('router/SET_Foot_Num', 1)
      // }
      // if (page === 'WdxmIndex') {
      //   this.$store.commit('router/SET_Foot_Num', 2)
      // }
      // if (page === 'Wdxm') {
      //   this.$store.commit('router/SET_Foot_Num', 2)
      // }
      // if (page === 'WdxmIndex') {
      //   this.$router.push('DljgWdxm')
      //   return
      // }
      this.$router.push({
        name: page
      })
    },
    getZmNrList() {
      if (this.zmList.length > 0) {
        const that = this
        const zmmklist = []
        this.zmList.forEach(function(v, i) {
          getMoudleHtml.url = '/app/zm/desktops/getDestopHtml/' + v.id + '/' + that.userInfo.id + '/' + that.userInfo.zzid + '?token=' + getToken()
          that.asyncApiModule(getMoudleHtml).then(res => {
            v.zmhtml = res
            zmmklist.push(v)
          })
        })
        if (zmmklist.length > 0) {
          this.zmList = zmmklist
        }
      }
    },
    getZmList() {
      const tempList = []
      getZmMoudle.url = '/app/zm/desktops/phone/' + this.userInfo.id + '?token=' + getToken()
      this.asyncApiModule(getZmMoudle).then(res => {
        // res = JSON.parse(res)
        res.forEach(function(v, i) {
          if (v.module_vList.length > 0) {
            v.module_vList.forEach(function(zmv, zmi) {
              zmv.zmhtml = '<div class="content"><div class="common-main box"><p align="center" style="font-size:14px;">加载中...</p></div></div>'
              tempList.push(zmv)
            })
          }
        })
        this.zmList = tempList
      }).catch(error => {
        this.$toast.fail(error)
      })
    },
    itemClick(id, bmgysid, sfdlbb, xmmc, bmgyszt) {
    // this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', item)
      this.$store.commit('gyswdxm/SET_XMID', id)
      this.$store.commit('gyswdxm/SET_BMGYSID', bmgysid)
      this.$store.commit('gyswdxm/SET_SFDLBB', sfdlbb)
      this.$store.commit('gyswdxm/SET_XMMC', xmmc)
      if (bmgyszt != null && bmgyszt >= 2) {
        this.$router.push({ name: 'Wdxmxmxq' })
      } else {
        this.$store.dispatch('gyswdxm/getStartGyslcAction', { id: bmgysid, bmrbh: this.userInfo.id }).then(res => {
          this.$router.push({ name: 'Wdxmxmxq' })
        })
      }
    }
  }
}
</script>

<style lang="css" scoped>

</style>

